import nem from 'nem-sdk';
import AccessController from './controllers/access';
import API from './api';

const api = new API();
/**
* @name TransferTransaction
* @description preparing and sending transaction
*/
/*
data: {
  amount: int,
  message: str,
  type: int,
  recipientPublicKey: recipient publick key,
  recipientAddress: recipient address,
  isMultisig: bool true/false,
  multisigAccount: str address

}
*/
let instance;
export default class TransferTransaction {
  constructor() {
    this.common = nem.model.objects.get('common');
    this.account = AccessController.create();
    this.network = {};
    this.nem = nem;
  }

  static create() {
    if (!instance) instance = new TransferTransaction();
    return instance;
  }

  prepare(type, data) {
    this.network.network = this.account.data.network;
    this.network.id = this.network.network === 'testnet' ? -104 : 104;
    switch (type) {
      case 'transferTransaction':
        return this.transferTransaction(data);
      case 'aggregateTransaction':
        return this.aggregateTransaction();
      default:
        return null;
    }
  }

  async transferTransaction(data) {
    if (!data) throw new Error({ code: 77, message: 'Transaction data isn\'t' });
    const transaction = nem.model.objects.get('transferTransaction');
    transaction.recipient = nem.model.address.clean(data.recipientAddress);
    if (!nem.model.address.isValid(transaction.recipient)) {
      throw new Error({ code: 79, message: 'Recipient address isn\'t valid' });
    }
    if (data.type === 2) {
      if (!nem.utils.helpers.isPublicKeyValid(data.recipientPublicKey)
          || !nem.model.address.toAddress(data.recipientPublicKey, this.network.network)
          === transaction.recipient) {
        throw new Error({ code: 80, message: 'Recipient publickey !== Recipient address' });
      }
    }
    this.common.privateKey = this.account.data.cosigners.Cosig1.privateKey;
    if (!nem.utils.helpers.isPrivateKeyValid(this.common.privateKey)) {
      throw new Error({ code: 80, message: 'Sender privatkey isn\'t valid' });
    }
    transaction.message = data.message ? data.message : '';
    transaction.amount = data.amount;
    transaction.messageType = data.type;
    transaction.recipientPublicKey = data.recipientPublicKey;
    if (data.isMultisig) {
      transaction.isMultisig = true;
      transaction.multisigAccount = this.account.data.master;
    }
    return nem.model.transactions
      .prepare('transferTransaction')(this.common, transaction, this.network.id);
  }

  aggregateTransaction() {
    const transaction = nem.model.objects.get('multisigAggregateModification');
    this.common.privateKey = this.account.data.master.privateKey;
    transaction.multisigAccount = this.account.data.master.publicKey;
    transaction.modifications.push(
      { modificationType: 1, cosignatoryAccount: this.account.data.cosigners.Cosig1.publicKey },
    );
    transaction.modifications.push(
      { modificationType: 1, cosignatoryAccount: this.account.data.cosigners.Cosig2.publicKey },
    );
    transaction.relativeChange = 2;
    return nem.model.transactions
      .prepare('multisigAggregateModificationTransaction')(this.common, transaction, this.network.id);
  }

  transfer(transactionEntity) {
    const $transactionEntity = transactionEntity;
    if ($transactionEntity.otherTrans) {
      const due = 12;
      $transactionEntity.deadline = $transactionEntity.timeStamp + due;
    }
    api.network = this.network.network;
    return api.send(this.common, $transactionEntity);
  }

  async Fee(type = 'transferTransaction', data) {
    const transaction = await this.prepare(type, data);
    if (typeof (transaction) !== 'object') {
      throw new Error({ code: 76, message: 'fee error' });
    }
    return Promise.resolve(
      Number(
        `${nem.utils.format.nemValue(transaction.fee)[0]}.${nem.utils.format.nemValue(transaction.fee)[1]}`,
      ),
    );
  }

  CounterByte(str) {
    return this.nem.utils.convert.rstr2utf8(str).length;
  }

  async Send(type, data) {
    try {
      const transaction = await this.prepare(type, data);
      return this.transfer(transaction);
    } catch (e) {
      throw new Error(e);
    }
  }
}



