import nem from 'nem-sdk';
import { GMTtoLocal } from './date';

const NETWORK = {
  '-1744830463': -104,
  '1744830465': 104
};

async function PreparePrivateMessage(data, recipientPrivate, type) {

  if (
    !data.transaction.otherTrans.timeStamp
    && !data.transaction.otherTrans.message
    && !data.transaction.signer
  ) throw new Error(`PrepatePrivateMessage: transaction isn't valid`)
  const timestamp = await GMTtoLocal(
    nem.utils.format.nemDate(data.transaction.otherTrans.timeStamp)
  );
  const hash = data.meta.hash.data.slice(0, 8);
  const message = data.transaction.otherTrans.message.type === 2 ? await nem.crypto.helpers.decode(recipientPrivate, data.transaction.signer, data.transaction.otherTrans.message.payload) : data.transaction.otherTrans.message.payload;
  const text = nem.utils.convert.utf82rstr(nem.utils.convert.hex2a(message));
  const sender = nem.model.address
    .toAddress(data.transaction.otherTrans.signer, NETWORK[data.transaction.version]);
  const address = type ? nem.model.address
    .toAddress(data.transaction.otherTrans.signer, NETWORK[data.transaction.version]) : data.transaction.otherTrans.recipient
  return {
    hash,
    recipient: {
      address,
      publicKey: data.transaction.otherTrans.signer,
    },
    payload: {
      timestamp,
      text,
      sender,
    },
  };
}
async function PrepareMessage(data) {
  if (
    !data.transaction.otherTrans.timeStamp
    && !data.transaction.otherTrans.message
    && !data.transaction.otherTrans.signer
    && !data.transaction.otherTrans.recipient
  ) throw new Error(`PrepateMessage: transaction isn't valid`);
  const timestamp = await GMTtoLocal(
    nem.utils.format.nemDate(data.transaction.otherTrans.timeStamp)
);
  const hash = data.meta.hash.data.slice(0, 8);
  const text = nem.utils.convert.utf82rstr(
    nem.utils.convert.hex2a(data.transaction.otherTrans.message.payload));
  const sender = nem.model.address
    .toAddress(data.transaction.otherTrans.signer, NETWORK[data.transaction.version]);

  return {
    hash,
    recipient: {
      address: data.transaction.otherTrans.recipient,
      publickey: data.transaction.otherTrans.signer,
    },
    payload: {
      timestamp,
      sender,
      text,
    },
  };
}
export { PrepareMessage, PreparePrivateMessage };



