import DialogController from '@/modules/controllers/dialog';
import store from '@/store';

const dialog = new DialogController();
async function AddDialog(data) {
  if (!data.type || !data.address) throw new Error('type or address not found');
  switch (data.type) {
    case 'chat':
      await dialog.addChat(data);
      break;
    case 'live':
      await dialog.addLive(data);
      break;
    case 'group':
      await dialog.addGroup(data);
      break;
  }
}
async function DelDialog(data) {
  switch (data.type) {
    case 'chats':
      store.dispatch('chats/delChat', data.address);
      store.dispatch('chats/addToBan', data.address);
      if (store.getters.getCurrent === data.address) await store.dispatch('setCurrent', null);
      break;
    case 'lives':
      await dialog.closeConnections(data);
      if (store.getters.getCurrent === data.address) await store.dispatch('setCurrent', null);
      store.dispatch('lives/delLive', data.address);
      break;
    case 'groups':
      await dialog.closeConnections(data);
      if (store.getters.getCurrent === data.address) await store.dispatch('setCurrent', null);
      store.dispatch('groups/delGroup', data.address);
      break;
  }
}
export { AddDialog, DelDialog };



