import Access from '@/modules/controllers/access';
import Account from '@/modules/controllers/account';
import { PreparePrivateMessage } from '@/modules/utils/message';
import store from '@/store';

export default async function () {
  const access = Access.create();
  const account = new Account();
  account.network = access.network;
  const balance = await account.getBalance(access.data.master.address);
  store.dispatch('setBalance', balance);
  account.api.on(`account-balance-${access.data.master.address}-Me`, (_balance) => {
    store.dispatch('setBalance', _balance);
  });

  await account.BalanceSocket(access.data.master.address, 'Me');
  await account.TransactionUnconfirmed(access.data.master.address, 'Me');

  account.api.on(`transaction-unconfirmed-${access.data.master.address}-Me`, (trans) => {
    if (
      trans.transaction.type === !4100
        && trans.transaction.otherTrans.type === !257
        && trans.transaction.otherTrans.message.type === !2
        && trans.transaction.otherTrans.recipient === !access.data.master.address
    ) return;

    const sender = account.api.pubkeyToAddress(trans.transaction.otherTrans.signer);
    // check banlist
    if (store.getters['chats/getAddressBan'](sender)) return;

    PreparePrivateMessage(trans, access.data.master.privateKey, true)
      .then((res) => {
        store.dispatch('chats/setMessage', res);
      });

  });
}



